* {
	font-family: "Roboto", sans-serif; }

header {
	position: relative;
	position: sticky;
	top: 0; }

nav {
	height: 50px;
	background-color: #212121; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;

	.menu_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300;
		text-decoration: none; }

	.menu_item {
		border-right: 2px dotted black;
		padding-right: 27px; } }

.subheader {
	position: absolute;
	width: 100%;
	height: 87px;
	background-color: rgba(0,0,0, .6);

	.subheader_logo {
		display: block;
		margin-top: 16px;
		max-width: 100%; }

	.subheader_official {
		margin-top: 6px;
		opacity: 0.5;
		font-weight: 400;
		font-size: 13px;
		color: #FFFFFF; }

	.subheader_call {
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right; }

	.subheader_phone {
		display: block;
		margin-top: 8px;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px;
		text-decoration: none; }

	.subheader_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		background-image: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-style: normal;
		font-weight: 400;
		font-size: 14px;
		text-transform: uppercase; } }

.promo {
	min-height: 800px;
	background: url('../icons/First/Layer3.png');
	display: flex;
	padding: 150px 0 135px 0;

	.promo_header {
		font-weight: 700;
		font-size: 60px;
		display: flex;
		text-transform: uppercase;
		color: #FFFFFF;
		line-height: 60px;
		margin: 0;
		text-align: center; }

	.promo_subheader {
		font-weight: 700;
		font-size: 36px;
		display: flex;
		text-transform: uppercase;
		color: #FFFFFF;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }

	.promo_descr {
		font-weight: 400;
		font-size: 18px;
		line-height: 24px;
		display: flex;
		text-align: center;
		color: #FFFFFF;
		margin-top: 23px; }

	.promo_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
		border-radius: 4px;
		border: none;
		font-weight: 400;
		font-size: 18px;
		text-transform: uppercase;
		color: #FFFFFF; } }

.reasons {
	padding: 71px 0 65px 0;

	.reasons_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px;

		.reasons_round {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 26px;
			width: 113px;
			height: 113px;
			background-color: #1EACC7;
			border-radius: 100%; }

		.reasons_descr {
			width: 343px;

			.reasons_text {
				font-weight: 300;
				font-size: 15px;
				line-height: 21px;
				color: #202020; }

			.reasons_subtitle {
				color: #1EACC7;
				line-height: 21px;
				font-size: 17px;
				font-weight: 400; } } } }

.label {
    margin: 0 auto;
    width: 112px;
    height: 24px;
    padding: 5px 12px;
    background-color: #000000;
    font-weight: 300;
    font-size: 11px;
    text-transform: uppercase;
    color: #FFFFFF;
    text-align: center; }

.title {
	margin-bottom: 0;
	margin-top: 38px;
	font-weight: 700;
	font-size: 38px;
	text-align: center;
	text-transform: uppercase;
	color: #222222 {
    text-align: center; } }

